<template>
    <div class="page-block page-block__palette bg-secondary flex-centered">

        <div class="container grid-xl" style="padding: 0;">

            <div class="columns col-gapless flex-centered">

                <div class="column col-4 col-md-6 col-sm-12" style="align-self: flex-end;">
                    <div class="page-container">
                        <div class="text-bold mb h4">Colors</div>
                        <div v-html="text"></div>
                    </div>
                </div>

                <div class="column col-8 col-md-6 col-sm-12">

                    <div class="columns col-oneline" style="margin: 0;">
                        <div class="pantone column col-1" v-for="(i,idx) in palette"
                            :style="`background:${i.hex}; color:${i.color}`">
                            <div>
                                <!--div class="text-semibold">Color #{{idx + 1}}</div-->
                                <div>{{i.rgb}}</div>
                                <div>{{i.hex}}</div>
                            </div>
                        </div>
                    </div>

                </div>
                
            </div>

        </div>

    </div>
</template>

<script>
    export default {
        props: {
            colors: {
                type: Array,
                default: () => (['#000000', '#999999', '#ffffff'])
            },
            text: {
                type: String,
                default: `We selected the color palette that matches the quality, corporate identity and nature of project.`
            }
        },
        computed: {
            palette() {
                return this.colors.map(i => this.pantone(i))
            }
        },
        methods: {
            pantone(hex) {
                const rgb = this.hex2Rgb(hex)
                return {
                    hex,
                    rgb,
                    color: this.getContrast50(hex)
                }
            },
            hex2Rgb(hex) {
                // https://stackoverflow.com/a/5624139
                // Expand shorthand form (e.g. "03F") to full form (e.g. "0033FF")
                var shorthandRegex = /^#?([a-f\d])([a-f\d])([a-f\d])$/i;
                hex = hex.replace(shorthandRegex, function (m, r, g, b) {
                    return r + r + g + g + b + b
                });

                var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex)
                /*
                  return result ? {
                      r: parseInt(result[1], 16),
                      g: parseInt(result[2], 16),
                      b: parseInt(result[3], 16)
                  } : null
                */
                return result ? `rgb(${parseInt(result[1], 16)},${parseInt(result[2], 16)},${parseInt(result[3], 16)})` : null

            },
            getContrast50(hexcolor) {
                hexcolor = hexcolor.replace("#", "");
                var r = parseInt(hexcolor.substr(0, 2), 16);
                var g = parseInt(hexcolor.substr(2, 2), 16);
                var b = parseInt(hexcolor.substr(4, 2), 16);
                var yiq = ((r * 299) + (g * 587) + (b * 114)) / 1000;
                return (yiq >= 128) ? 'black' : 'white';
            }
        }
    }
</script>