<template>
    <div class="page-block page-block__client">
        <div class="columns col-gapless">
            <div class="column col-8 col-md-6 col-sm-12 bg-secondary">
                <h3 class="page-container page-block__client__about flex-centered container grid-sm">
                    <div>
                        <slot></slot>
                    </div>
                </h3>
            </div>
            <div class="column col-4 col-md-6 col-sm-12" :style="`background:${color}`">
                <div class="page-container page-block__client__info flex-centered-vertical">
                    <slot name="additional" v-if="additional"></slot>
                    <ul class="ul-clear ul-nomarkers" v-else>
                        <li class="mb" v-if="task">
                            <b class="d-block">Task</b>
                            <div v-html="task"></div>
                        </li>
                        <li v-if="deliverables">
                            <b class="d-block">Deliverables</b>
                            <div v-html="deliverables"></div>
                        </li>
                        <li class="mt" v-if="url">
                            <b class="d-block">Visit site</b>
                            <a :href="`//${url}`" rel="nofollow" target="_blank"
                                v-if="typeof url == 'string'">{{url}}</a>
                            <a :href="`${url.link}`" rel="nofollow" target="_blank" v-else>{{url.name}}</a>
                        </li>
                        <li class="mt" v-if="openSource">
                            <a :href="`${openSource}`" rel="nofollow" target="_blank">Open Source</a>
                        </li>
                        <li class="mt" v-if="licence">
                            <b class="d-block">Licence</b>
                            <span>{{licence}}</span>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            color: {
                type: String,
                default: '#f2f2f2'
            },
            task: String,
            deliverables: {
                type: String,
                default: 'Web development',
            },
            url: [String, Object],
            openSource: String,
            licence: String
        },
        computed: {
            additional() {
                return !!this.$slots.additional
            },
        }
    }
</script>