import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

import routes from './routes'

let router = new VueRouter({
    routes,
    base: process.env.BASE_URL || '/',
    mode: 'history',
})


router.beforeEach((to, from, next) => {
    document.title = (to.meta.name && !to.meta.title) ? `${to.meta.name} - Marcius Studio` : 'Marcius Studio: Full-stack web development'
    next()
})

export default router