<template>
    <div>
        <!--button-->
        <button class="btn-menu btn btn-rounded z-index-max">
            <input type="checkbox" class="menu-checkbox" id="menu-checkbox" v-model="isOpen">
            <label for="menu-checkbox" />
        </button>
        <!--menu-->
        <div id="menu" class="menu scroll-wrapper" v-show="show">
            <item v-for="(i,idx) in schema" :i="i" :key="idx" :id="`menu${idx+1}`" @toggle="toggle" :isOpen="isOpen" />
        </div>
    </div>
</template>

<script>
    import item from './item'

    export default {
        data: () => ({
            isOpen: false,
            show: false, // need to keep close animation
            schema: [
                {
                    name: 'Cases',
                    route: 'cases',
                    about: 'We let our work do the talking',
                    background: '#171719',
                },
                {
                    name: 'About',
                    route: 'about',
                    about: 'Online studio specialised in web development',
                    background: '#222224',
                },
                {
                    name: 'Contacts',
                    about: `Feel free to ask questions`,
                    background: '#000',
                }
            ]
        }),
        watch: {
            isOpen(val) {

                if (val) {
                    this.show = true
                    this.submit()
                    return false
                }

                this.submit()
            }
        },
        methods: {
            submit() {
                const duration = 400

                this.$anime({
                    targets: ['#menu1 .menu-background', '#menu2 .menu-background', '#menu3 .menu-background'],
                    delay: this.$anime.stagger(100, { easing: 'easeOutQuad' }),
                    width: this.isOpen ? '100%' : 0,
                    easing: 'easeOutQuad',
                    duration,
                    complete: (anim) => {
                        if (!this.isOpen) this.show = false
                    },
                })

                this.$anime({
                    targets: ['#menu1 .menu-details', '#menu2 .menu-details', '#menu3 .menu-details'],
                    delay: this.$anime.stagger(this.isOpen ? 100 : 0, { easing: 'easeOutQuad' }),
                    translateX: this.isOpen ? [-20, 0] : -20,
                    opacity: this.isOpen ? 1 : 0,
                    easing: 'easeOutQuad',
                    duration,
                })
            },
            toggle(val) {
                this.isOpen = val
            }
        },
        components: {
            item
        }
    }
</script>