<template>
    <div class="page-block page-block__client">
        <div class="columns col-gapless">
            <div class="column col-5 col-md-6 col-sm-12 flex-centered bg-secondary h100 ">
                <div class="page-container text-center">
                    <div class="page-block__text">{{customer}}</div>
                    <div class="page-block__text"> —— </div>
                    <div class="page-block__text" v-if="isShowReview">Review</div>
                    <div v-else>
                        <a class="link link-padding" :href="video" target="_blank" v-show="video">Video Review</a>
                        <span class="mr05" v-show="video">|</span>
                        <a class="link link-padding" :href="linkedin" target="_blank" v-show="linkedin">LinkedIn</a>
                        <a class="link link-padding" :href="telegram" target="_blank" v-show="telegram">Telegram</a>
                        <a class="link link-padding" :href="vkontakte" target="_blank" v-show="vkontakte">VKontakte</a>
                    </div>
                </div>
            </div>
            <div class="column col-7 col-md-6 col-sm-12  flex-centered ">
                <h3 class="page-container page-block__text container grid-sm">
                    <slot></slot>
                </h3>
            </div>
        </div>
        <div class="divider show-sm"></div>
    </div>
</template>

<script>
    export default {
        props: {
            customer: String,
            review: String,
            video: String,
            linkedin: String,
            telegram: String,
            vkontakte: String,
        },
        computed: {
            isShowReview() {
                return !(this.video || this.linkedin || this.telegram || this.vkontakte)
            }
        }
    }
</script>