            </div>
            <div class="menu-details__subname mt">
                <div v-html="i.about"></div>
                <links v-if="i.name.toLowerCase() == 'contacts'" contacts="true" />
                <links class="mt" v-if="i.name.toLowerCase() == 'contacts'" />
            </div>
            
        </div>

        <div class="menu-background" :style="`background:${i.background}`"></div>
    </div>
</template>

<script>
    import { isMobile } from 'mobile-device-detect'

    export default {
        props: ['i'],
        methods: {
            submit() {
