<template>
    <div class="logo-container flex-centered" @click="submit">
        <span ref="arrow" class="material-icons" style="font-size: 1.2rem; opacity: 0; position: absolute; pointer-events: none;">
            keyboard_backspace
        </span>
        <div ref="logo" class="logo">
            <icon-svg name="logo" width="2rem" height="2rem"></icon-svg>
        </div>
    </div>
</template>

<script>
    const duration = 500

    export default {
        computed: {
            show() {
                return this.$route.name != 'cases'
            }
        },
        watch: {
            '$route.name'(val) {
                this.show ? this.onHover() : this.onLeave()
            }
        },
        methods: {
            onHover() {
                if (this.show)
                    this.$anime.timeline({
                        targets: this.$refs.logo,
                        translateX: 30,
                        duration,
                        easing: 'easeOutExpo',
                    }).add({
                        targets: this.$refs.arrow,
                        translateX: -10,
                        opacity: 1,
                        duration,
                        easing: 'easeOutExpo',
                    })
            },
            onLeave() {
                this.$anime.timeline({
                    targets: this.$refs.arrow,
                    opacity: 0,
                    translateX: 0,
                    duration,
                    easing: 'easeOutExpo',
                }).add({
                    targets: this.$refs.logo,
                    translateX: 0,
                    duration,
                    easing: 'easeOutExpo',
                })
            },
            submit() {
                if (this.show) this.$router.push({ name: 'cases' }) // this.$router.go(-1)
            },
        },
    }
</script>

<style lang="scss">
    .logo-container {
        position: absolute;
        cursor: pointer;
    }

    .logo {
        display: inherit;
    }

    .background--light {
        color: black;
    }

    .background--dark {
        color: white;
    }

    .background--complex {
        color: gray;
    }

